@import url(https://fonts.googleapis.com/css?family=Titillium+Web&display=swap);
body {
  margin: 0;
  font-family: 'Titillium Web', sans-serif;
  color: white;
}

.App {
  background-color: black; 
  min-height: 100vh;
}
.Nav-Bar {
  height: 80px;
  width: 100%;
  background-color: black;
  box-shadow: 0px 8px 9px 17px #000000bd;
  position: fixed;
  top: 0;
  left: 0;
  padding: 0 30px;
  z-index: 100;
  display: flex; }
  .Nav-Bar .back-icon {
    height: 24px;
    margin-top: 26px; }
  .Nav-Bar .nav-header {
    margin: 21px 0 0 30px;
    font-size: 24px; }
  .Nav-Bar .search-bar-holder {
    position: absolute;
    right: 30px;
    padding-top: 24px; }
    .Nav-Bar .search-bar-holder .search-icon {
      height: 24px; }
    .Nav-Bar .search-bar-holder .search-bar {
      margin-left: 15px; }
      .Nav-Bar .search-bar-holder .search-bar input {
        border-radius: 15px;
        padding: 3px 15px;
        color: black; }
      .Nav-Bar .search-bar-holder .search-bar input:focus {
        outline: none; }

.Video-Holder {
  margin: 79px 0 0 0;
  padding: 31px 15px 0 15px; }

.Video-Content {
  margin-bottom: 90px;
  padding: 0 15px 0 15px; }
  .Video-Content .Poster {
    width: 100%;
    margin-bottom: 24px; }
  .Video-Content .Content-Header {
    font-size: 20px; }

